import React from 'react'
import NavStyle from "../styles/Nav.js"
import { Link } from "react-router-dom"
import logo from "../media/icon.png"
import { FiGithub } from "react-icons/fi"
import { DiNpm } from "react-icons/di"

export default function Nav() {
    return (
        <NavStyle className="Nav">

            <div id="pageLinks">
                <Link to="/">
                    <img id="icon" src={logo} alt="wave js icon" />
                </Link>

                <Link to="/">
                    <button>Playground</button>
                </Link>
                <Link to="/docs">
                    <button>Docs</button>
                </Link>
                {/* <Link to="/player">
                    <button>React Player</button>
                </Link>
                <Link to="/video/playground">
                    <button>Video Recorder</button>
                </Link> */}
            </div>

            <div id="externalLinks">
                <a href="https://github.com/foobar404/Wave.js" target="_blank">
                    <FiGithub></FiGithub>
                </a>
                <a href="https://www.npmjs.com/package/@foobar404/wave" target="_blank">
                    <DiNpm></DiNpm>
                </a>
            </div>
        </NavStyle>
    )
}
