import styled from "styled-components"


export default styled.main`
    height: 100%;
    width: 100%;
    

    section{
        height: 92%;
        width: 100%;
        display: flex;

        .DocsDisplay{
            height: 100%;
            width:85%;
            overflow: auto;
            background: #0f1724;
            color:white;
            padding-bottom: 100px;
            box-sizing: border-box;
            scroll-behavior:smooth;

            & > *{
                width: 70%;
                margin:auto;
            }

            pre,p,h1,h2,ul{
                margin-bottom: 20px;
            }

            i{
                color:aqua;
                font-style:normal;
                font-weight: bold;
            }

            li{
                font-size:16px;
                font-weight: bold;
            }

            a{
                color:#b900ff;
            }

            p{
                border-radius:15px;
                background: #212c3d;
                padding: 20px;
            }

            h1{
                font-size:40px;
                font-weight: bold;
                padding-bottom: 20px;
                border-bottom:1px solid #fff8;
            }
            h2{
                font-size:25px;
            }
        }

        .DocsNavigation{
            height: 100%;
            width: 15%;
            background: #212c3d;
            padding-left: 20px;

            h1:hover{
                color:yellow;
            }
            h2:hover{
                color:deeppink;
            }

            a.active>h1{
                color:yellow;
            }
            a.active>h2{
                color:deeppink;
            }

            a{
                text-decoration: none;
            }

            span{
                display: block;
                height: 1px;
            }

            h1,h2{
                color:#eee;
            }
            h1{
                font-size:20px;
                font-weight: bold;
            }
            h2{
                font-size:16px;
                margin-left: 10px;
            }
        }
    }
`