import styled from "styled-components"

export default styled.section`
    height: 100vh;
    width: 100vw;
    background: black;

    & *::-webkit-scrollbar {
        width: 6px;
        height: 6px;
    }

    /* Track */
    & *::-webkit-scrollbar-track {
        background: #222;
    }

    /* Handle */
    & *::-webkit-scrollbar-thumb {
        background: linear-gradient(0deg,#7F00FF,#E100FF);
    }

`