import React, { useState, useContext } from 'react'
import { TwitterPicker } from 'react-color';
import rainbow from "rainbow-colors-array"
import { Context } from "../App.js"


export default function PlaygroundColorPicker() {
    let { state, dispatch } = useContext(Context)
    let [activeColorIndex, setActiveColorIndex] = useState(0)
    let [activeColorList, setActiveColorList] = useState([true])

    function handleChange(color) {
        dispatch({
            type: "UPDATE_PREVIEW_COLOR",
            payload: {
                wave: state.activePreviewWave,
                index: activeColorIndex,
                color: color.hex
            }
        })
    }

    function handleColorIndex(index) {
        setActiveColorIndex(index)

        let list = []
        list[index] = true
        setActiveColorList(list)
    }

    return (
        <div className="PlaygroundColorPicker">
            <div id="activeColorBox">

                {state.previewWaveColorMap[state.activePreviewWave].map((color, i) => {
                    return (
                        <div
                            onClick={() => handleColorIndex(i)}
                            className={`colorCube ${activeColorList[i] && "active"}`}
                            style={{ background: color }}
                            key={i}
                        >

                        </div>)
                })}

            </div>

            <TwitterPicker triangle="hide" onChangeComplete={handleChange} colors={rainbow(65, "hex").map(v => "#" + v.hex)} />
        </div>
    )
}


