import React, { useState, useEffect, useContext } from 'react'
import Wave from "@foobar404/wave"
import { Context } from "../App.js"
import { FaArrowRight, FaArrowLeft } from "react-icons/fa"

export default function PlaygroundWaveSelection() {
    let [wave] = useState(new Wave())

    let { state, dispatch } = useContext(Context)
    let [page, setPage] = useState(0)

    let waves = ["bars", "bars blocks", "big bars", "cubes", "dualbars", "dualbars blocks", "fireworks",
        "flower", "flower blocks", "orbs", "ring", "rings", "round wave", "shine", "shine rings", "shockwave",
        "star", "static", "stitches", "web", "wave"]

    let waveSection = waves.slice(10 * page, (10 * page) + 10)

    let previewElements = waveSection.map((waveType, i) => {
        i += page * 10
        return (
            <div className="preview" key={i}>
                <p>{waveType}</p>
                <canvas onClick={() => handleClick(waveType)} id={`wavePreview_${i}`}></canvas>
            </div>
        )
    })

    useEffect(() => {
        waveSection.map((waveType, i) => {
            i += page * 10
            wave.fromElement("player", `wavePreview_${i}`, { type: waveType, colors: [...state.previewWaveColorMap[waveType]] })
        })
    }, [page, state.previewWaveColorMap])

    function handleClick(wave) {
        dispatch({ type: "UPDATE_ACTIVE_WAVE", payload: wave })
    }

    return (
        <div className="PlaygroundWaveSelection">
            <div id="previewBox"> {previewElements}</div>

            <PageNavigation {...{ setPage, page, max: Math.ceil(waves.length / 10) - 1 }} />

        </div>
    )
}


function PageNavigation(props) {

    function handleClick(amt) {
        //page is 0 based
        let page = props.page + amt
        if (page < 0) page = 0
        if (page > props.max) page = props.max

        props.setPage(page)
    }

    return (
        <div className="PageNavigation">
            <div className="arrow" onClick={() => handleClick(-1)}><FaArrowLeft /></div>
            <div>{props.page + 1}</div>
            <div className="arrow" onClick={() => handleClick(1)}><FaArrowRight /></div>
        </div>
    )
}
