import React, { useContext, useEffect } from 'react'
import { Context } from "../App.js"
import PlaygroundStyle from "../styles/Playground.js"

import Nav from "../components/Nav.js"
import AudioPlayer from "../components/AudioPlayer.js"
import PlaygroundCanvas from "../components/PlaygroundCanvas.js"
import PlaygroundWaveSelection from "../components/PlaygroundWaveSelection.js"
import PlaygroundColorPicker from "../components/PlaygroundColorPicker.js"

export default function Playground({ location }) {
    let { dispatch } = useContext(Context)

    useEffect(() => {
        if (location.search) {
            let wave = location.search.split("=")[1].split("_").join(" ")
            dispatch({ type: "UPDATE_ACTIVE_WAVE", payload: wave })
        }
    }, [])

    return (
        <PlaygroundStyle className="Playground">
            <Nav></Nav>
            <section>
                <PlaygroundWaveSelection />
                <PlaygroundCanvas />
                <PlaygroundColorPicker />
            </section>
            <AudioPlayer></AudioPlayer>
        </PlaygroundStyle>
    )
}
