import styled from "styled-components"

export default styled.main`
    height: 100%;
    width: 100%;

    section{
        width: 100%;
        height: 82%;

        display: flex;
    }

    .PlaygroundCanvas{
        width: 60%;
        height: 100%;
    }

    .PlaygroundColorPicker{
        width: 20%;
        height: 100%;

        #activeColorBox{
            display: flex;
            border-bottom: 1px solid #777;
            padding-bottom: 15px;

            .colorCube{
                height: 50px;
                width: 50px;
                border-radius:10px;
                margin-right: 10px;
                cursor: pointer;
                box-sizing: border-box;
            }

            .colorCube:hover{
                border: 4px solid #eeea;
            }

            .active{
                border: 4px solid #eee;
            }
        }

        .twitter-picker{
            background: #111 !important;
        }
    }

    .PlaygroundWaveSelection{
        width:20%;
        height: 100%;

        #previewBox{
            display: flex;
            flex-wrap:wrap;
            height: 90%;
            width: 100%;
        }


        .preview{
            height: 20%;
            width: 50%;
            display: flex;
            flex-direction:column;
            
            p{
                color:white;
                text-align: center;
                height: 20%;
                margin: 0;
                font-weight: bold;
            }

            canvas{
                height: 80%;
                width:100%;
                cursor: pointer;
            }
        }
        
        .PageNavigation{
            width: 100%;
            height: 10%;
            display: flex;
            justify-content: space-around;
            align-items: center;
            color:white;
            font-size: 22px;

            .arrow{
                height: 40px;
                width: 40px;
                cursor: pointer;

                display: flex;
                align-items: center;
                justify-content: center;
            }

            .arrow:hover{
                color:#f31f81;
            }   

    
        }
    }

`