import styled from "styled-components"

export default styled.nav`
    height: 8%;
    background: #2c1120;
    border-bottom: 1px solid #000;
    display: flex;
    justify-content: space-between;


    #externalLinks{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 20%;


        a{
            height: 35px;
            width: 35px;
            padding: 10px;

            border-radius:50%;
            color:white;
        }

        a:hover{
            background: #fff2;
            color:lime;
        }

        svg{
            height: 100%;
            width: 100%;
        }
    }

    #icon{
        height: 100%;
        margin-bottom: 10px;
        margin-left: 10px;
    }


    div{
        width: auto;
        height: 100%;
        z-index: 2;

        display: flex;
        align-items: center;
    }


    a{
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        button{
            background: transparent;
            border:none;
            height: 100%;

            color:white;
            font-weight: bold;
            font-size: 16px;
            cursor: pointer;
            transition:.25s ease;
        }

        &:nth-child(2) button:hover{
            color:#FF355E;
            background: #fff2;
        }

        &:nth-child(3) button:hover{
            color:#FF9933;
            background: #fff2;
        }

        &:nth-child(4) button:hover{
            color:#CCFF00;
            background: #fff2;
        }

        &:nth-child(5) button:hover{
            color:#50BFE6;
            background: #fff2;
        }
    }



`