import React, { useState, useEffect, useContext } from 'react'
import { Context } from "../App.js"
import Wave from "@foobar404/wave"


export default function PlaygroundCanvas(props) {
    let [wave] = useState(new Wave())
    let { state, dispatch } = useContext(Context)

    useEffect(() => {
        wave.fromElement("player", "playgroundCanvas", {
            type: state.activePreviewWave,
            colors: state.previewWaveColorMap[state.activePreviewWave]
        })
    }, [state.activePreviewWave, state.previewWaveColorMap])

    return (
        <section className="PlaygroundCanvas">
            <canvas id="playgroundCanvas" height={window.innerHeight * .90} width={window.innerWidth * .60}></canvas>
        </section>
    )
}
