import React from 'react'

export default function DocsNavigation({ isVisible }) {

    let classMap = {}

    for (let key in isVisible) {
        if (isVisible[key]) {
            classMap[key] = "active"
        }
    }

    return (
        <div className="DocsNavigation">
            <a href="#docs_install" className={classMap.install}><h1>Install</h1></a>
            <a href="#docs_npm" className={classMap.npm}><h2>NPM</h2></a>
            <a href="#docs_cdn" className={classMap.cdn}><h2>CDN</h2></a>

            <span></span>

            <a href="#docs_setup" className={classMap.setup}><h1>Setup</h1></a>

            <span></span>

            <a href="#docs_usage" className={classMap.usage}><h1>Usage</h1></a>
            <a href="#docs_visuals" className={classMap.visuals}><h2>Visuals</h2></a>
            <a href="#docs_options" className={classMap.options}><h2>Options</h2></a>
            <a href="#docs_functions" className={classMap.functions}><h2>Functions</h2></a>
            <a href="#docs_events" className={classMap.events}><h2>Events</h2></a>

            <span></span>

            <a href="#docs_contribute" className={classMap.contribute}><h1>Contribute</h1></a>

        </div>
    )
}
