import React, { useState } from 'react'
import DocsStyle from "../styles/Docs.js"

import Nav from "../components/Nav.js"
import DocsNavigation from "../components/docs/DocsNavigation.js"
import DocsDisplay from "../components/docs/DocsDisplay.js"

export default function Docs() {
    let [isVisible, setIsVisible] = useState({})
    return (
        <DocsStyle>
            <Nav />
            <section>
                <DocsNavigation {...{ isVisible }} />
                <DocsDisplay {...{ setIsVisible }} />
            </section>
        </DocsStyle>
    )
}
