import React, { useState, useEffect } from 'react'
import AudioPlayerStyle from "../styles/AudioPlayer.js"
import { FaPlay, FaPause } from "react-icons/fa"
import { AiFillSound, AiFillFileAdd } from "react-icons/ai"
import { TiArrowForward, TiArrowBack } from "react-icons/ti"
import { IoMdMusicalNotes } from "react-icons/io"
import Slider from 'react-rangeslider'
import Wave from "@foobar404/wave"




export default function AudioPlayer() {
    let [wave] = useState(new Wave())
    let [audioElement, setAudioElement] = useState(null)
    let [state, setState] = useState({ playing: false, volume: .5, time: 0, duration: 1000 })


    useEffect(() => {

        //prep audio & state
        const elm = document.querySelector("#player")
        setAudioElement(elm)
        handleState("duration", (!elm.duration) ? 1000 : elm.duration)
        elm.volume = .5

        //create wave visual
        wave.fromElement("player", "playerCanvas", { type: "wave", colors: ["silver", "white"] })

        //update time tracker
        let timeLoop = setInterval(() => {
            handleState("time", elm.currentTime)
        }, 500)

        return () => {
            clearInterval(timeLoop)
        }
    }, [])

    function handleClick(action) {
        switch (action) {
            case "back":
                audioElement.currentTime -= 5
                break
            case "toggle":
                let playing = audioElement.paused
                if (playing) audioElement.play()
                else audioElement.pause()
                setState(state => {
                    return {
                        ...state,
                        playing
                    }
                })
                break
            case "forward":
                audioElement.currentTime += 5
                break
        }
    }

    function handleState(key, value) {
        setState(state => {
            return {
                ...state,
                [key]: value
            }
        })
    }

    function updateTime(value) {
        handleState("time", value)
        audioElement.currentTime = value
    }

    function updateVolume(value) {
        handleState("volume", value)
        audioElement.volume = value
    }

    function changeSong(song) {
        audioElement.src = song
        audioElement.play()
        handleState("playing", true)
    }

    function uploadFile() {
        let fileElm = document.getElementById("importSong");
        fileElm.click()

        fileElm.addEventListener("change", () => {
            let file = fileElm.files[0]
            if (!file.type.startsWith("audio"))
                return
            let src = URL.createObjectURL(file)

            audioElement.src = src
            audioElement.play()
            handleState("playing", true)
        }, { once: true })


    }



    return (
        <AudioPlayerStyle className="AudioPlayer">
            <div className="playerButtons">
                <div>
                    <button onClick={() => handleClick("back")}><TiArrowBack></TiArrowBack></button>
                    <button onClick={() => handleClick("toggle")} > {state.playing ? <FaPause /> : <FaPlay />}</button>
                    <button onClick={() => handleClick("forward")} > <TiArrowForward></TiArrowForward></button>
                </div>

                <div id="trackSlider">
                    <Slider value={state.time} className="Slider" min={0} max={state.duration} onChange={updateTime} />
                </div>

                <div id="volumeSlider">
                    <AiFillSound />
                    <Slider value={state.volume} className="Slider" min={0} max={1} step={.05} onChange={updateVolume} />
                </div>

                <div id="songSelection">

                    <button onClick={() => changeSong("http://radio2.wsradio.net:8090/jazzcafe320")}>
                        <IoMdMusicalNotes />
                    </button>
                    <button onClick={() => changeSong("http://igor.torontocast.com:1940/stream")}>
                        <IoMdMusicalNotes />
                    </button>
                    <button onClick={() => changeSong("http://streamingecuador.net:8001/radiodisney.mp3")}>
                        <IoMdMusicalNotes />
                    </button>
                    <button onClick={() => changeSong("http://stream.dancewave.online:8080/dance.mp3")}>
                        <IoMdMusicalNotes />
                    </button>
                    <button onClick={() => changeSong("http://server-26.stream-server.nl:8300/stream")}>
                        <IoMdMusicalNotes />
                    </button>

                </div>

                <div>
                    <button onClick={uploadFile}><AiFillFileAdd /></button>
                    <input hidden type="file" name="" id="importSong" />
                </div>
            </div>

            <canvas className="playerCanvas" id="playerCanvas" height="80" width={window.innerWidth}></canvas>

            <audio hidden id="player" src="http://radio2.wsradio.net:8090/jazzcafe320" crossOrigin="anonymous"></audio>
        </AudioPlayerStyle>
    )
}


