import styled from "styled-components"

export default styled.div`
    width: 100vw;
    height: 10%;
    position: fixed;
    bottom:0;
    left:0;


    background:
        linear-gradient(5deg, #4f4f4f, transparent),
        linear-gradient(185deg, #49185d, black),
        linear-gradient(90deg, #232e6b, black);

    background-blend-mode: screen;
    color:white;
    display: flex;
    flex-direction: column;

    .playerButtons{
        height: 55%;
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-bottom: 1px solid #fff2;

        & > div{
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;

            button{
                height: 100%;

                background: #845EC2;
                border:none;
                color:white;
                transition:.25s ease;
                cursor: pointer;
                outline:1px solid #fff4;
            }
            button:hover{
                background: linear-gradient(90deg,#845EC2,#008F7A);
            }
        }
        svg{
            height: 65%;
            min-width:70px;
        }
    }

    .playerCanvas{
        height: 45%;
        width: 100%;
    }


    #trackSlider{
        width: 30%;
    }

    #volumeSlider{
        width: 10%;
   

        .Slider{
            width: 85%;
        }
    }


    .Slider{
        height: 12px;
        width: 100%;
        border-radius: 10px;
        background: #e6e6e6;
        position:relative;

        .rangeslider, .rangeslider__fill{
            display: block;
            box-shadow:inset 0 1px 3px rgba(0,0,0,.4);
            border-radius: 10px;
            background: #509bff;
            height: 100%;
        }
        .rangeslider__handle{
            height: 20px;
            width: 20px;
            border-radius:50%;
            background: #000;
            position: absolute;
            top:50%;
            transform: translate3d(-50%,-50%,0);
            cursor: pointer;
            box-shadow: 0 1px 3px rgba(0,0,0,.4), 0 -1px 3px rgba(0,0,0,.4);
        }
        .rangeslider__handle-tooltip{
            position: absolute;
            bottom:100%;
        }
    }
     
    
`